import * as React from 'react';
import { Inline, Controller } from '@ali/4ever-cangjie';
import Image from './components/image';
import { ImagePluginConfig } from './types';

interface IProps {
  node: Inline;
  controller: Controller
  imageConfig: ImagePluginConfig;
  isTransferring?: boolean;
  uploadPlaceholder?: string;
  uploadProgress?: number;
  onLoad?: () => void;
  onDoubleClick?: () => void;
  onContextMenu?: (event: any) => void;
  onClick? : (event: React.MouseEvent) => void;
  onMouseDown?: () => void;
}

export default React.forwardRef((props: IProps, ref: React.Ref<HTMLImageElement>) => {
  const {
    node, controller, imageConfig, isTransferring, uploadPlaceholder, uploadProgress, onLoad, ...rest
  } = props;

  return (
    <Image
      {...rest}
      ref={ref}
      node={node}
      controller={controller}
      imageConfig={imageConfig}
      isTransferring={isTransferring}
      uploadPlaceholder={uploadPlaceholder}
      uploadProgress={uploadProgress}
      onLoad={onLoad}
    />
  );
});
